@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;800&display=swap');

/* wrapper div */
.App {
  font-family: "Poppins", Arial, sans-serif;
  font-weight: 600;
  margin: 0;
  padding: 0;
}



/* classes for the side bar and its elements */
#side-bar {
  background-image: linear-gradient(to top left, #7fcd9f, #658bbf);
  position: fixed;
  float: left;
  background-color: grey;
  padding: 0;
  height: 100%;
  width: 15vw;
  
}

#side-main {
  height: 100%;
  float: left;
  width: 100%; /* is set to calc(100% - 45px) when size < 1024px */
  overflow-y: auto;
  overflow-x: hidden;
  direction: rtl;
} 

#side-arrow {
  float: right;
  display: none; /* is set to flex when size is under 1024px */
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 100vh;
}

#side-logo {
  font-family: "Poppins", Arial, sans-serif;
  background-color: transparent;
  border: none;
  padding: 0;
  
  margin-top: 2%;
  margin-right: 12.5%;
  font-weight: 800;
  text-align: center;
  justify-content: center;
  font-size: calc(5px + 2vw);
  color: white;
  text-decoration: underline;
  text-decoration-color: #a0e281;
  text-decoration-thickness: 3px;
}

#side-logo:hover {
  text-decoration: underline;
  text-decoration-color: white;
  text-decoration-thickness: 3px;
}

.side-label {
  font-family: "Poppins", Arial, sans-serif;
  font-weight: 600;
  width: 100%;
  padding: 5%;
  background-color: transparent;
  text-align: left; 
  color: white;
  border: 0;  
  padding-top: 2.5%;
  padding-bottom: 2.5%;
}

.side-label:hover {
  background-color: rgba(255,255,255,0.25);
  transition: 0.2s;
}

.group {
  margin-bottom: 5%;
}

button.section {
  font-size: calc(7px + 1vw);
  margin-left: 5%;
}
button.subsection {
  width: 100%;
  padding-left: 15%;
  padding-right: 10%;
  font-size: calc(5px + .67vw);
  font-weight: 300;
}



/* classes for side scrollbar */
/* width */
#side-main::-webkit-scrollbar {
  width: 7px;
}

/* Track */
#side-main::-webkit-scrollbar-track {
  background: transparent; 
}
 
/* Handle */
#side-main::-webkit-scrollbar-thumb {
  background: #666;
  border-radius: 10px;
}

/* Handle on hover */
#side-main::-webkit-scrollbar-thumb:hover {
  background: #444  ; 
}



/* wrapper for top-right social media icons */
#socials {
  position: absolute;
  right: 4%;
  margin-top: 2%;

}

#socials > * {
  margin-left: calc(5px + 1vw);
}



/* wrapper for non-side panel */
#main-body {
  margin-top: 2%;
  width: 85%;
  float: right;
  text-align: left;
  justify-content: center;
}

.card {
  padding-left: 4%;
  padding-right: 2%;
  width: 92%;
  font-size: calc(5px + .5vw);
  display: flex;
  margin-bottom: 2%;
  /* border: 1px solid black; */
}

.p-left {
  padding-left: 4%;
}

.p-right {
  padding-right: 4%;
}

hr.divide-card {
  border-top: 1px solid black;
  border-left: none;
  border-right: none;
  border-bottom: none;
  color: black;
  margin-left: 6%;
  margin-right: 6%;
  margin-bottom: 2%;
}


.card-minor {
  width: 40%;
  text-align: right;
  /* border: 1px solid blue; */
  
}

.card-major {
  width: 60%;
  margin-top: auto;
  margin-bottom: auto;
  /* border: 1px solid red; */
}

#img-text {
  font-weight: 400;
  width: calc(75px + 25vw);
  margin-left: 2%;
  font-size: calc(7px + 1vw);
  justify-content: middle;
  margin-top: auto;
  margin-bottom: auto;
  min-width: 200px;
}

.section-header {
  font-size: calc(10px + 2vw);
  margin-left: 4%;
  font-weight: 800;
  color: white;
  -webkit-text-stroke: 1px #a0e281;
}



/* other style definitions */
img {
  max-width: 100%;
  vertical-align: middle;
  border-radius: calc(5px + 1vw);
}

h2.minor {
  font-family: "Poppins", Arial, sans-serif;
  font-weight: 300;
}

.no-btn {
  background-color: transparent;
  height: 100%;
  border: 0;
  width: 100%;
}
.no-btn:hover {
  background-color: rgba(255,255,255,0.25);
  transition: 0.2s;
}

a {
  width: 100%;
  font-weight: 600;
  font-size: calc(5px + 1.25vw);
}

a.none {
  color: black;

}

a.h2 { /* class used for the projects so that it looks like h2 but can be licked on */
  color: black;
  font-size: calc(5px + .5vw);
}

a.h2:hover {
  color: gray;
}

.App-logo {
  height: calc(50px + 12.5vw);
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  color: white; 
}
/* add toggling ability for the side bar  */
@media only screen and (max-width: 900px) {
  #side-bar { 
      width: 250px;
      /* display: none;*/
      margin-left: -205px;
  }
  #side-main {
    width: calc(100% - 45px);
  }
  #side-logo {
    font-size: 30px;
  }
  #side-arrow {
    display: flex;
  }
  button.section {
    font-size: 20px;
    padding-right: 5%; 
    padding-left: 5%;
  }
  button.subsection {
    width: 100%;
    padding-left: 15%;
    padding-right: 10%;
    font-size: 16px;
    font-weight: 300;
  }
  /* */
  #side-bar-arrow {
    display: fixed;
  }
  #main-body {
    width: calc(100vw - 65px);
    padding-left: 4%;
    padding-right: 2%;
  }

}